import React from "react";
import styled from "styled-components";
import TopBar from "../components/TopBar";
import { Title } from "../components/Title";
import Background from "../components/Background";
import about1 from "../images/about1.jpg";
import about2 from "../images/about2.jpg";
import about3 from "../images/about3.jpg";
import about4 from "../images/about4.jpg";
import about5 from "../images/about5.jpg";
import about6 from "../images/about6.jpg";
import { Button } from "../components/Button";
import { withRouter } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowCircleLeft } from "@fortawesome/free-solid-svg-icons";

const StyledBackground = styled(Background)`
  padding: 0 20px;

  img.screen {
    width: 70%;
    display: block;
    margin: 0 auto;
    box-shadow: 0px 0px 50px -10px white;
    @media only screen and (min-width: 1024px) {
      width: 35%;
    }
  }
`;

const Description = styled.p`
  margin: 20px auto;
  color: white;
  width: 70%;
  letter-spacing: 0.7px;
  line-height: 1.5;
  @media only screen and (min-width: 1024px) {
    width: 50%;
  }
`;

const BoldDescription = styled(Description)`
  font-weight: bold;
`;
const DescriptionSmall = styled(Description)`
  font-size: 12px;
  text-align: center;
  a {
    color: white;
  }
`;

const ChapterTitle = styled.h3`
  color: #d4b726;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: underline;
  font-size: 24px;
  text-align: center;
  margin: 20px 0;
`;
const StyledButton = styled(Button)`
  margin: 0 auto;
`;

const Buttons = styled.div`
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  margin: 30px 0;
`;

const GoBackButton = styled(Button)`
  position: absolute;
  top: 60px;
  left: 40px;
  color: #d4b726;
  font-size: 28px;
  @media only screen and (min-width: 1024px) {
    top: 80px;
  }

  svg {
    pointer-events: none;
  }
  &::before,
  &::after {
    display: none;
  }
`;
const AboutTemplate = (props) => {
  const goToMenu = (e) => {
    e.preventDefault();
    if (e.target.innerHTML === "Rozegraj Turniej") {
      props.history.push("/newcup");
    } else {
      props.history.push("/");
    }
  };

  const goBack = () => {
    props.history.push("/");
  };
  return (
    <StyledBackground className="stats">
      <TopBar>
        <Title>O Aplikacji</Title>
      </TopBar>
      <GoBackButton onClick={goBack}>
        <FontAwesomeIcon icon={faArrowCircleLeft} />
      </GoBackButton>

      <ChapterTitle>1. Motywacja dla stworzenia aplikacji</ChapterTitle>
      <Description>
        Zapewne wiele razy z koleg miae ten problem umawiajc si na wsplne
        granie w <strong>Fif</strong>.
      </Description>
      <BoldDescription>
        "LOSUJEMY DRUYNY. EBY BYO SPRAWIEDLIWIE."
      </BoldDescription>
      <Description>
        Zawsze jednak z moim koleg Dominikiem (
        <span role="img" aria-label="hand with fingers splayed">
          
        </span>
        ), stawiani bylimy przed jednym problemem...
        <br />
        <br />
        Chcielimy eby zespoy byy losowane tylko z{" "}
        <strong>okrelonej puli druyn</strong>, na przykad tylko 4 najlepsze
        druyny z topowych 5 lig europejskich. <br /> Wszyscy gracze Fify wiedz
        jednak, e niestety losowanie w grze odbywa si jedynie w ramach{" "}
        <strong>jednej ligi</strong> i z gry oboje wiecie z jakiej ligi bd
        zespoy przez Was wylosowane. Dodatkowo los moe sprawi e bdzie to
        pojedynek midzy Barcelon a Espanyolem. <br />
        Mao w tym sprawiedliwoci...
        <br />
        <br />
        Oczywicie istnieje w Fifie moliwo utworzenia turnieju i wprowadzenia
        tam swoich druyn, jednak gdy spotykasz si z koleg na piwko i chcesz
        szybko rozegra par partii, tworzenie turnieju jest{" "}
        <strong>zawsze</strong> zbyt mudnym procesem, nie oszukujmy si....
        <br />
        <br /> Odpowiedzi na ten niewtlpiwy problem pierwszego wiata jest
        <strong> FIFA Draw</strong>.
      </Description>
      <ChapterTitle>2. Jak uywa?</ChapterTitle>
      <Description>1. Wprowad imiona graczy</Description>
      <img className="screen" src={about1} alt="Wybieranie imion uczestnikw" />
      <Description>
        2.Wybierz zespoy z ktrych bd losowne druyny dla Ciebie i Twojego
        przeciwnika.
      </Description>
      <img className="screen" src={about2} alt="Wybieranie druyn" />
      <Description>
        3. Po dodaniu zespow do koszyka, przechodzimy do losowania druyn.
        Moesz losowa po jednym zespole, lub wylosowa wszystkie na raz.
      </Description>
      <img className="screen" src={about3} alt="Losowanie druyn" />
      <Description>
        4. Po rozstrzygniciu meczu (spotkanie nie moe zakoczy si remisem,
        wic jeli tak si stanie - rozegrajcie dogrywk) - wprowad rezultat.
        Wynik zostanie zapisany na tablicy.
      </Description>
      <img className="screen" src={about4} alt="Zapisywanie wyniku" />
      <Description>
        5. Turniej moe zakoczy si na 2 sposoby:
        <br />
        - sami decydujecie, o zakoczeniu turnieju (Przycisk: "Zakocz
        turniej"), <br />- dalsze rozgrywanie spotka nie wpynie na kocowy
        wynik (Przykad: 6 meczw wygranych przeze mnie, 3 przez Dominika, 2
        mecze do rozegrania - w takiej sytuacji Dominik nawet jeli wygra
        wszystkie mecze nie ma szans na sukces w turnieju), aplikacja sama
        wykryje tak sytuacj i zakoczy turniej za nas.
      </Description>
      <img className="screen" src={about5} alt="Koniec turnieju" />
      <Description>
        6. Wszystkie turnieje s zapisywane w lokalnej pamici przegldarki.
      </Description>
      <img className="screen" src={about6} alt="Statystyki" />

      <Description>
        7. Teraz dzwo do kolegi i zaczynajcie swj pierwszy totalnie bezstronny
        turniej w Fif z pomoc <strong>FIFA Draw</strong>!
      </Description>
      <Buttons>
        <StyledButton onClick={goToMenu}>Powrt do Menu</StyledButton>
        lub
        <StyledButton onClick={goToMenu}>Rozegraj Turniej</StyledButton>
      </Buttons>
      <DescriptionSmall>
         2019 Design and Coded by <a href="https://codeverse.pl">Codeverse</a>.
        Wszystkie prawa zastrzeone.
      </DescriptionSmall>
    </StyledBackground>
  );
};

export default withRouter(AboutTemplate);
